import React, { Component } from 'react'
import { connect } from 'react-redux'
import { CSSTransition } from 'react-transition-group'
import { actionCreators }  from './store'
import { actionCreators as loginActionCreators} from '../../pages/login/store'
import { 
  HeaderWrapper,
  Logo,
  Nav,
  NavItem,
  NavSearch,
  Addition,
  Button,
  SearchInfo,
  SearchInfoTitle,
  SearchInfoSwitch,
  SearchInfoList,
  SearchInfoItem,
  SearchWrapper,
 } from './style'
import { Link } from 'react-router-dom'

class Header extends Component {

  getListArea() {
    const { focused, list, page, totalPage, handleMouseEnter, handleMouseLeave, mouseIn, handleChangePage } = this.props
    const newList = list.toJS()
    const pageList = []

    if(newList.length){
      for(let i = (page - 1) * 10; i < page * 10; i++){
        pageList.push(
          <SearchInfoItem key={newList[i]}>{newList[i]}</SearchInfoItem>
        )
      }
    }
    
    if(focused || mouseIn){
      return (
        <SearchInfo onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave}>
          <SearchInfoTitle>
            
            <SearchInfoSwitch onClick={() => handleChangePage(page, totalPage)}></SearchInfoSwitch>
          </SearchInfoTitle>
          <SearchInfoList>
            {/* {
                list.map((item) => {
                // console.log(item);
                return <SearchInfoItem key={item}>{item}</SearchInfoItem>
              })
            } */}
            {pageList}
          </SearchInfoList>
        </SearchInfo>
      )
    } else {
      return null
    }
  }

  render() {
    const { focused, handleInputFocus, handleInputBlur, list, login, logout } = this.props
    return (
      <>
        <HeaderWrapper>
            <Logo/>
          <Nav>
          <NavItem className='left active'></NavItem>
          <NavItem className='left'>App</NavItem>
          {
            login ? <NavItem onClick={logout} className='right'></NavItem> : <Link to='/login'><NavItem className='right'></NavItem></Link>
          }
          <NavItem className='right'>Aa</NavItem>
          {/* in --  true or false   -- timeout=200 */}
          <CSSTransition in={focused} timeout={200} classNames='slide'>
            <SearchWrapper>
              <NavSearch className={focused ? 'focused' : ''} onFocus={() => handleInputFocus(list)} onBlur={handleInputBlur}></NavSearch>
              <div className='iconfont'></div>
              {/*  */}
              {this.getListArea()}
            </SearchWrapper>
          </CSSTransition>
        </Nav>
        <Addition>
          <Link to='/write'>
            <Button className='writing'></Button>
          </Link>
          <Button className='reg'></Button>
        </Addition>
      </HeaderWrapper>
    </>
    )
  }
}


